/*Crypto Section*/
.crp__item {
	position: relative;
	padding: 30px;
	border: 1px solid var(--border-clr);
	@include border-radius(5px);
	height: 100%;
	.crp--link {
		position: absolute;
		inset: 0;
	}
	&-icon {
		font-size: 40px;
		line-height: 1;
		color: var(--base-clr);
		margin-bottom: 25px;
	}
	&-cont {
		@include transition($transition);
		&-title {
			margin-bottom: 20px;
			@include transition($transition);
		}
	}
	@include transition($transition);
	&:hover {
		@include transform(translateY(-5px));
		.crp__item-cont {
			&-title {
				color: var(--base-clr);
			}
		}
	}
}
.crypto-table-wrapper {
	overflow: hidden;
	@include border-radius(10px);
	border: 1px solid var(--border-clr);
	.crypto-table-header {
		padding: 15px;
	}
}
.table {
	font-size: 15px;
	color: var(--body-clr);
	margin: 0;
	* {
		border-color: var(--border-clr);
	}
	@include breakpoint(xl) {
		.badge {
			min-width: 70px;
		}
	}
	thead {
		background: var(--primary-clr);
		th {
			padding: 17px 0;
			border: none;
			color: var(--white-clr);
		}
		margin-left: -1px;
		margin-right: -1px;
	}
	border-left: 1px solid var(--border-clr);
	border-right: 1px solid var(--border-clr);
	tbody {
		tr {
			td {
				padding: 15px 0;
				vertical-align: middle;
			}
			&:nth-of-type(even) {
				background: rgba(var(--title-rgb), 0.031) !important;
			}
		}
	}
	text-align: center;
	thead {
		th {
			font-weight: 500;
		}
	}
}
.table-buyer {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	max-width: 200px;
	margin: 0 auto;
	img {
		width: 40px;
		height: 40px;
		object-fit: cover;
		@include border-radius(50%);
	}
	.subtitle {
		padding-left: 15px;
		font-size: 17px;
		font-weight: 400;
	}
	@include breakpoint(max-sm) {
		img {
			width: 30px;
			height: 30px;
		}
		.subtitle {
			font-size: 15px;
			padding-left: 10px;
		}
	}
}
@include breakpoint(max-xxl) {
	.table--mobile-xxl {
		padding-right: 5px;
		&:not(.no--scroll) {
			max-height: 70vh;
			overflow-x: hidden;
			overflow-y: auto;
			&::-webkit-scrollbar-track {
				box-shadow: inset 0 0 6px rgba(var(--title-rgb), 0.3);
				width: 4px;
			}
			&::-webkit-scrollbar {
				width: 4px;
			}
			&::-webkit-scrollbar-thumb {
				background-color: var(--base-clr);
				outline: 1px solid var(--base-clr);
				width: 4px;
			}
		}
		.table {
			width: 100%;
			border-left: 1px solid var(--border-clr);
			border-right: 1px solid var(--border-clr);
			border-top: 1px solid var(--border-clr);
			text-align: left;
			thead {
				display: none;
			}
			tbody {
				tr {
					display: block;
					td {
						display: flex;
						justify-content: space-between;
						align-items: center;
						&::before {
							content: attr(data-label);
							font-family: var(--heading-fonts);
							font-weight: 500;
							color: var(--title-clr);
						}
						border: none;
						padding: 3px 0;
					}
					border-bottom: 1px solid var(--border-clr);
				}
			}
		}
		.table-buyer {
			margin-right: 0;
		}
	}
}
